import { TERMO_COMPENSACAO_HORAS } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosAvisoPrevioTrabalhadoDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    ' Nos termos dos artigos 487 a 491 da C.L.T., ' +
    'fica V.Sa. avisada que decidimos rescindir o seu contrato de trabalho, ' +
    'devendo cumprir o Aviso Prévio que iniciará em <DATA_INICIAL> e terminará em <DATA_FINAL>.\n ' +
    'Neste período, sua jornada diária de trabalho será reduzida em DUAS horas:\n\n' +
    ' (   ) entrará (mais tarde) às ............... horas.\n\n' +
    ' (   ) sairá (mais cedo) às ............... horas.\n\n' +
    ' (   ) sairá ............... dia(s) antes do final do Aviso Prévio.\n \n ' +
    'NOTA: É necessária a apresentação da carteira profissional para as devidas anotações.';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return TERMO_COMPENSACAO_HORAS;
  }
}
