import { CARTA_SUSPENSAO } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosCartaSuspensaoDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    '\nIlmo(a). Sr(a). \n\n' +
    +'<TRABALHADOR_SUSPENSO> \n\n\n' +
    +'Prezado Senhor(a):\n' +
    +'  Pela presente, comunicamo-nos a sua suspensão pelo prazo de (<QUANTIDADE_DIAS>) ' +
    'dias, a partir de <DATA_INICIO_SUSPENSAO> por ter V.Sª. praticado ato passível de ' +
    'punição. Solicitamos providências para a não repetição de tal fato, no futuro, devendo ' +
    'V.Sª. voltar ao trabalho no próximo dia <DATA_FIM_SUSPENSAO>, apresentando-se ao serviço ' +
    'para a assinatura de seu ponto como de costume, sob pena de iniciarmos a partir dessa ' +
    'data, a contagem de ausência ao serviço para caracterizar o abandono de emprego.\n\n\n\n\n\n' +
    'Atenciosamente, \n\n\n\n ' +
    '--------------------------------------------------------------\n' +
    '<RESPONSAVEL>\n\n\n\n' +
    'Recebi o presente comunicado em <DATA_RECEBIMENTO_CARTA>\n\n\n' +
    '--------------------------------------------------------------\n' +
    '<TRABALHADOR_SUSPENSO>';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return CARTA_SUSPENSAO;
  }
}
