import { ABONO_PECUNIARIO } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosAbonoPecuniarioDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    '\t\t\t(Parág. 1§, art. 143 da CLT, c/ alter. DL 1535 de 13/04/77)\n\n\n' +
    +'EMPREGADOR: <EMPRESA_NOME>\n' +
    +'ENDEREÇO: <EMPRESA_ENDERECO>\n\n' +
    +'EMPREGADO: <TRABALHADOR_NOME>\n' +
    +'CTPS (Nº/Série/UF): <TRABALHADOR_CTPS>\n' +
    +'PERÍODO AQUISITIVO: <PERIODO_AQUISITIVO>\n\n\n' +
    'O empregado abaixo assinado, funcionário desta empresa, vem respeitosamente requerer que lhe seja concedido' +
    ' um terço do período de suas próximas férias, a que tem direito, em abono pecuniário, ficando a critério da' +
    ' empresa a designação da data da época da concessão, de acordo com a Consolidação das Leis de Trabalho.\n\n';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return ABONO_PECUNIARIO;
  }
}
