import { CONTRATO_ESTAGIO } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosContratoEstagioDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    'CONCEDENTE: <EMPRESA_NOME> com sede em <EMPRESA_CIDADE>, na <EMPRESA_RUA>, nº <EMPRESA_NUMERO>, bairro <EMPRESA_BAIRRO>, CEP <EMPRESA_CEP>, no estado de <EMPRESA_ESTADO>, inscrita no C.N.P.J./C.P.F. sob o nº <EMPRESA_CNPJ>, neste ato o estagiário sendo representado pelo(a) Sr(a). <RESPONSAVEL_NOME>, <RESPONSAVEL_NACIONALIDADE>, <RESPONSAVEL_ESTADO_CIVIL>, <RESPONSAVEL_PROFISSAO>, Carteira de Identidade nº <RESPONSAVEL_RG>, C.P.F. nº <RESPONSAVEL_CPF>, residente e domiciliado à Rua <RESPONSAVEL_RUA>, <RESPONSAVEL_NUMERO>, Bairro <RESPONSAVEL_BAIRRO>, CEP. <RESPONSAVEL_CEP>.\n' +
    ' \n' +
    'ESTAGIÁRIO:  <ESTAGIARIO_NOME>, <ESTAGIARIO_NACIONALIDADE>, <ESTAGIARIO_ESTADO_CIVIL>, Estagiário, Carteira de Identidade nº <ESTAGIARIO_RG>, C.P.F. nº <ESTAGIARIO_CPF>, residente e domiciliado na <ESTAGIARIO_RUA>, na cidade de <ESTAGIARIO_CIDADE>, no estado de <ESTAGIARIO_ESTADO>.\n' +
    ' \n' +
    'As partes acima identificadas têm, entre si, justo e acertado o presente Termo de Compromisso de Estágio, que se regerá pelas cláusulas seguintes e pelas condições descritas no presente.\n' +
    ' \n' +
    'DO OBJETO DO CONTRATO\n' +
    ' \n' +
    'Cláusula 1ª. O presente tem como OBJETO a prestação de serviços a ser feita pelo ESTAGIÁRIO que se encontra matriculado no curso de <ESTAGIARIO_CURSO> da(o) <ESTAGIARIO_INSTITUICAO>.\n' +
    ' \n' +
    'Cláusula 2ª. O objetivo do presente instrumento é a experiência prática do aprendizado teórico, aperfeiçoamento técnico, científico e de relacionamento humano, de forma a complementar o ensino e a aprendizagem em consonância com o calendário escolar.\n' +
    ' \n' +
    'DA JORNADA DE TRABALHO\n' +
    ' \n' +
    'Cláusula 3ª. O ESTAGIÁRIO terá uma jornada total de <ESTAGIARIO_QUADRO_HORARIO> horas semanais, sendo cumpridas de <HORA_ENTRADA> às <HORA_SAIDA> horas diárias, no período de <MESES> meses, compatível com o horário escolar.\n' +
    ' \n' +
    'Cláusula 4ª. No período das férias escolares os contratantes estabelecerão os critérios específicos em comum acordo, para o cumprimento do mesmo.\n' +
    '\n' +
    'DO COMPROMISSO\n' +
    ' \n' +
    'Cláusula 5ª. No decorrer da vigência do contrato, o ESTAGIÁRIO se compromete a realizar todas as atividades requeridas pela CONCEDENTE, ressalvando-se aquelas incompatíveis com o aprendizado técnico-escolar.\n' +
    ' \n' +
    'Cláusula 6ª. O ESTAGIÁRIO se compromete a prestar informações ou esclarecimentos quando solicitado e/ou requerido pela Instituição de Ensino a qual estuda e/ ou, no cumprimento de suas funções.\n' +
    ' \n' +
    'DA REMUNERAÇÃO\n' +
    ' \n' +
    'Cláusula 7ª. As atividades exercidas regularmente serão remuneradas por meio de bolsa-estágio, no valor de R$ <ESTAGIARIO_SALARIO>, pago mensalmente em dinheiro, até o quinto dia útil subsequente ao trabalhado.\n' +
    ' \n' +
    'Cláusula 8ª. A bolsa referida acima, não configura remuneração trabalhista, portanto pode ser modificada mediante ajuste das partes.\n' +
    ' \n' +
    'Cláusula 9ª. O ESTAGIÁRIO está incluso(a) na cobertura do seguro contra acidentes pessoais de trabalho, mediante a apólice nº <NUMERO_APOLICE> da Instituição <INSTITUICAO_SEGURO>.\n' +
    ' \n' +
    'DA RESCISÃO\n' +
    ' \n' +
    'Cláusula 10ª. As partes poderão interromper, rescindir ou renovar por tempo indeterminado o presente instrumento, desde que comunicado expresso por escrito.\n' +
    ' \n' +
    'Cláusula 11ª. A rescisão se fará por ato unilateral desde que comunicada expressamente pela parte interessada com antecedência mínima de uma semana, bem como, se o ESTAGIÁRIO agir de forma prejudicial em relação ao CONCEDENTE\n' +
    ' \n' +
    'DO PRAZO\n' +
    ' \n' +
    'Cláusula 12ª. O presente compromisso terá validade de <MESES> meses, a iniciar-se em <DATA_INICIO> e findar-se até <DATA_FIM>.\n' +
    ' \n' +
    'CONDIÇÕES GERAIS\n' +
    ' \n' +
    'Cláusula 13ª. O presente contrato passa a vigorar a partir da assinatura.\n' +
    ' \n' +
    'Cláusula 14ª. O presente instrumento não se configura sob nenhuma forma vínculo empregatício.\n' +
    ' \n' +
    'Cláusula 15ª. Ao final do semestre o ESTAGIÁRIO elaborará relatório completo das atividades exercidas o qual será assinado pela CONCEDENTE e pela Instituição de Ensino.\n' +
    ' \n' +
    'Cláusula 16ª. A Instituição de Ensino a qual o ESTAGIÁRIO está diretamente vinculado se compromete também por meio deste instrumento a remeter semestralmente comprovante de matrícula do mesmo.\n' +
    'Cláusula 17ª.  Por estarem assim justos e contratados, firmam o presente instrumento, em duas vias de igual teor, juntamente com 2 (duas) testemunhas.\n' +
    ' \n';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return CONTRATO_ESTAGIO;
  }
}
