import { VALE_TRANSPORTE_DESISTENCIA } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosValeTransporteDesistenciaDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    'Prezados Senhores: \n\n' +
    +'Pelo presente, venho desistir da utilização do Vale Transporte, previsto na Lei nº: 7.418, de 16/12/1985. \n' +
    +'Com o advento da Lei nº: 7.619 de 30/09/1987, e a nova Regulamentação, pelo Decreto nº: 95.247 de 17/11/1987, \n' +
    +'sujeitando-me às penalidades previstas em lei.\n\n' +
    +'Esta decisão é válida, enquanto não necessitar deste recurso legal, por tempo indeterminado, de acordo com minha livre e espontânea vontade';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return VALE_TRANSPORTE_DESISTENCIA;
  }
}
