import { CARTA_ADVERTENCIA_FALTA } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosCartaAdvertenciaFaltaDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    '\n<EMPRESA_CIDADE>, <DATA_EXTENSO>. \n\n\n' +
    +'Pelo presente fica <TRABALHADOR_ADVERTIDO> advertido(a) pelas faltas ' +
    +'discriminadas abaixo: \n\n' +
    +'FALTAS NOS DIAS <DATAS_OCORRENCIA> SEM NENHUMA INFORMAÇÃO AO EMPREGADOR. \n\n\n' +
    +'Espero(amos) que de futuro procure não incorrer em novas faltas, afim ' +
    'de que a firma não se veja obrigada a usar do direito que lhe confere o ' +
    'Artigo 482 da Consolidação das Leis do Trabalho (Decreto-Lei nº 5.452 de ' +
    '1º de Maio de 1.943).\n\n\n\n\n' +
    'Ciente: \n\n\n' +
    '_______________________________________\n' +
    'Funcionário \n\n\n' +
    '_______________________________________\n' +
    'Empregador';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return CARTA_ADVERTENCIA_FALTA;
  }
}
