export function getTermoDefault(customizacaoTipoEnum: string) {
  const result = getAllDefaultContents().find(it => it.getCustomizacaoTermosTipoEnum().value === customizacaoTipoEnum);
  return result === null ? null : result.getTermoDefault();
}

