import { VALE_REFEICAO_RECIBO } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosValeRefeicaoReciboDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    'Recibo de Vale Refeição' +
    '\n\n\n' +
    'Recebi da empresa <EMPRESA_NOME>, os vales refeição descritos abaixo:\n' +
    'Utilizarei para alimentação no período <DATA_INICIO> a <DATA_FIM>.\n' +
    'Estou ciente que a empresa poderá descontar até <DESCONTO>% do valor dos vales fornecidos.\n';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return VALE_REFEICAO_RECIBO;
  }
}
