import { CONTRATO_TRABALHO_MENOR_APRENDIZ } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosContratoTrabalhoMenorAprendizDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    'IDENTIFICAÇÃO DAS PARTES\n' +
    +'\n \n' +
    +'Empregador: <EMPRESA_RAZAO_SOCIAL>, com sede na <EMPRESA_LOGRADOURO>, nº <EMPRESA_NUMERO>, Bairro <EMPRESA_BAIRRO>, CEP <EMPRESA_CEP>, <EMPRESA_CIDADE> - <EMPRESA_ESTADO>, inscrito no CNPJ sob o nº <EMPRESA_CNPJ>, e no Cadastro Estadual sob o nº <EMPRESA_INSCRICAO_ESTADUAL>, neste ato representado por <RESPONSAVEL_EMPRESA_NOME>, <RESPONSAVEL_EMPRESA_NACIONALIDADE>, <RESPONSAVEL_EMPRESA_ESTADO_CIVIL>, <RESPONSAVEL_EMPRESA_CARGO>, Carteira de Identidade nº <RESPONSAVEL_EMPRESA_RG>, CPF. nº <RESPONSAVEL_EMPRESA_CPF>, residente e domiciliado na(o) <RESPONSAVEL_EMPRESA_LOGRADOURO>, nº <RESPONSAVEL_EMPRESA_NUMERO>,  <RESPONSAVEL_EMPRESA_BAIRRO>, CEP <RESPONSAVEL_EMPRESA_CEP>, <RESPONSAVEL_EMPRESA_CIDADE> - <RESPONSAVEL_EMPRESA_ESTADO>.\n' +
    +'\n' +
    +'Empregado aprendiz: <APRENDIZ_NOME>, <APRENDIZ_NACIONALIDADE>, <APRENDIZ_ESTADO_CIVIL>, <APRENDIZ_CARGO>, Carteira de Identidade nº <APRENDIZ_RG>, CPF nº <APRENDIZ_CPF>, Carteira de Trabalho nº <APRENDIZ_CTPS_NUMERO> e série <APRENDIZ_CTPS_SERIE>, residente e domiciliado na(o) <APRENDIZ_LOGRADOURO>, nº <APRENDIZ_NUMERO>, <APRENDIZ_BAIRRO>, <APRENDIZ_CEP>, <APRENDIZ_CIDADE> - <APRENDIZ_ESTADO>.\n' +
    +'\n' +
    +'Responsável legal: <RESPONSAVEL_APRENDIZ_NOME>, <RESPONSAVEL_APRENDIZ_NACIONALIDADE>, <RESPONSAVEL_APRENDIZ_ESTADO_CIVIL>, <RESPONSAVEL_APRENDIZ_CARGO>, Carteira de Identidade nº <RESPONSAVEL_APRENDIZ_RG>, C.P.F. nº <RESPONSAVEL_APRENDIZ_CPF>, residente e domiciliado na(o) <RESPONSAVEL_APRENDIZ_LOGRADOURO>, nº <RESPONSAVEL_APRENDIZ_NUMERO>, <RESPONSAVEL_APRENDIZ_BAIRRO>, <RESPONSAVEL_APRENDIZ_CEP>, <RESPONSAVEL_APRENDIZ_CIDADE> - <RESPONSAVEL_APRENDIZ_ESTADO>.\n' +
    +'\n' +
    +'As partes identificadas acima celebram entre si o presente contrato de aprendizagem, regido pelas cláusulas seguintes e demais disposições legais vigentes:\n' +
    +'\n' +
    +'DAS DISPOSIÇÕES LEGAIS\n' +
    +'\n' +
    +'Cláusula 1ª – O presente contrato de aprendizagem tem como fundamento legal as disposições contidas na Consolidação das Leis do Trabalho (CLT), no Decreto nº 5.598/2005 e demais atos normativos que regem o assunto. \n' +
    +'\n' +
    +'DOS OBJETIVOS\n' +
    +'\n' +
    +'Cláusula 2ª - O empregador admite aos seus serviços o empregado, comprometendo-se a submetê-lo ao programa de formação técnico-profissional metódica mediante matrícula e frequência no curso de <APRENDIZ_CURSO>, mantido pela <INSTITUICAO_ENSINO_NOME>, situada na(o) <INSTITUICAO_ENSINO_LOGRADOURO>, nº <INSTITUICAO_ENSINO_NUMERO>, bairro <INSTITUICAO_ENSINO_BAIRRO>, CEP <INSTITUICAO_ENSINO_CEP>, na cidade de <INSTITUICAO_ENSINO_CIDADE>, no estado de <INSTITUICAO_ENSINO_ESTADO> para exercer tarefas da prática profissional em ambiente apropriado do empregador na função de <APRENDIZ_CARGO>.\n' +
    +'\n' +
    +'DA VIGÊNCIA DO CONTRATO DE APRENDIZAGEM\n' +
    +'\n' +
    +'Cláusula 3ª - O presente contrato terá duração de <APRENDIZ_PERIODO_CONTRATO> dias, tendo início em <DATA_INICIO_CONTRATO> e será concluído em <DATA_FIM_CONTRATO>, correspondendo à duração do contrato de aprendizagem.\n' +
    +'\n' +
    +'DA JORNADA DE TRABALHO\n' +
    +'\n' +
    +'Cláusula 4ª – O contrato de aprendizagem será executado pelo empregado numa jornada diária de <APRENDIZ_JORNADA_DIARIA> horas, de <APRENDIZ_DIA_INICIO_SEMANA> a <APRENDIZ_DIA_FIM_SEMANA>, perfazendo um total de <APRENDIZ_JORNADA_SEMANAL> horas semanais. \n' +
    +'\n' +
    +'DA REMUNERAÇÃO\n' +
    +'\n' +
    +'Cláusula 5ª – Ao empregado, ressalvadas outras condições mais favoráveis, será garantido o salário mínimo hora de acordo com os dias de frequência à escola e ao local de prestação dos serviços do empregador, nas atividades de formação técnico-profissional. \n' +
    +'\n' +
    +'DAS OBRIGAÇÕES DAS PARTES\n' +
    +'\n' +
    'Cláusula 6ª - O contrato de aprendizagem será devidamente anotado na Carteira de Trabalho e Previdência Social (CTPS) do empregado, assegurado todos os direitos trabalhistas e previdenciários previstos na legislação trabalhista e previdenciária, especialmente no que se tange ao período de concessão das férias, as quais coincidirão com o período de férias escolares nas hipóteses previstas em lei. \n' +
    '\n' +
    'Cláusula 7ª – O empregado assume o compromisso de apresentar ao seu empregador, no final de cada mês, o comprovante de frequência escolar às aulas do respectivo mês, bem como o atestado de aproveitamento do curso no respectivo período da aprendizagem em que estiver matriculado. \n' +
    '\n' +
    'Cláusula 8ª – O empregado tem obrigação de: \n' +
    '\n' +
    'a) frequentar as aulas e participar de outras atividades escolares definidas pela entidade que lhe está proporcionando o programa de formação técnico-profissional em que estiver matriculado; \n' +
    '\n' +
    'b) executar as tarefas programadas no estabelecimento do empregador; \n' +
    '\n' +
    'c) cumprir as normas disciplinares e regulamentares do estabelecimento do empregador durante todo o período de vigência do contrato de aprendizagem; \n' +
    '\n' +
    'd) respeitar a legislação de segurança e saúde no trabalho de acordo com as orientações do empregador. \n' +
    '\n' +
    'DAS HIPÓTESES DE EXTINÇÃO CONTRATUAL\n' +
    '\n' +
    'Cláusula 9ª - O presente contrato de aprendizagem se extinguirá nas seguintes situações: \n' +
    '\n' +
    'a) na data prevista para seu término estipulado neste instrumento; \n' +
    '\n' +
    'b) quando o aprendiz completar 24 (vinte e quatro) anos de idade, salvo no caso de aprendiz portador de deficiência, situação em que não há limite de idade; \n' +
    '\n' +
    'c) desempenho insuficiente ou inadaptação do aprendiz, comprovado através de laudo de avaliação elaborado pela entidade executora da aprendizagem; \n' +
    '\n' +
    'd) falta disciplinar grave (justa causa) seja praticada pelo empregado, seja por parte do Empregador, caracterizada por quaisquer das hipóteses descritas nos artigos 482 e 483 da CLT; \n' +
    '\n' +
    'e) ausência injustificada à escola que implique perda do ano letivo; \n' +
    '\n' +
    'f) a pedido do aprendiz; \n' +
    '\n' +
    'g) fechamento da empresa em virtude de falência, encerramento das atividades da empresa e morte do empregador constituído em empresa individual. \n' +
    '\n' +
    'Cláusula 10 - Por estarem assim, justos e acordados, firmam o presente contrato de aprendizagem em 02 (duas) vias de igual teor, juntamente com 2 (duas) testemunhas.\n';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return CONTRATO_TRABALHO_MENOR_APRENDIZ;
  }
}
