    'Recibo de Vale Alimentação\n' +
    +'\n' +
    +'\n' +
    +'Recebi da empresa <EMPRESA_NOME>, os vales alimentação descritos abaixo.\n' +
    +'Utilizarei para alimentação no período <DATA_INICIO> a <DATA_FIM>.\n' +
    +'Estou ciente que a empresa poderá descontar até <DESCONTO>% do valor dos vales fornecidos.';

