import { DECLARACAO_MAIS_DE_UM_VINCULO_EMPREGATICIO } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosDeclaracaoOutroVinculoEmpregaticioDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    'Eu, <TRABALHADOR_NOME>, declaro, para os devidos fins sob as penas da lei, que nos meses de <MES_INICIO> a <MES_FINAL> de <ANO> prestei serviços à(s) empresa(s) <EMPRESA_NOME>, CNPJ nº <EMPRESA_CNPJ>, sendo remunerado no valor de R$ <TRABALHADOR_SALARIO>, com retenção de contribuição previdenciária no valor do teto máximo do salário-de-contribuição da Previdência Social.\n' +
    +'Portanto, fica dispensada nova retenção de contribuição previdenciária.\n' +
    +'_______________________________________\n' +
    +'Nome e assinatura do contribuinte\n' +
    +'<NUMERO_PIS_NIS>\n' +
    '<EMPRESA_CIDADE>, <DATA_FIM_EXTENSO>\n' +
    'O segurado deverá manter sob sua guarda cópia da declaração referida acima, juntamente com os comprovantes de pagamento, para fins de apresentação ao INSS ou à fiscalização da Receita Federal, quando solicitado.';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return DECLARACAO_MAIS_DE_UM_VINCULO_EMPREGATICIO;
  }
}
