import { PEDIDO_DEMISSAO } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosDemissaoDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    ' Ficam V. Sas. avisados que, nos termos do artigo 487 da C.L.T., ' +
    'decorridos <QUANTIDADE_DIAS_AVISO_PREVIO> dias a partir de <DATA_INICIAL> deixarei de prestar serviços a essa Empresa, ' +
    'servindo esta de Aviso Prévio que terminará no dia <DATA_FINAL>.\n\n ' +
    'NOTA: É necessária a apresentação da carteira profissional para as devidas anotações.';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return PEDIDO_DEMISSAO;
  }
}
