import { CONTRATO_TRABALHO_TEMPO_INDETERMINADO } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosContratoTrabalhoTempoIndeterminadoDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    'Pelo presente instrumento e na melhor forma de direito, as partes:\n' +
    +'\n' +
    +' 1.<RESPONSAVEL_NOME>, <RESPONSAVEL_NACIONALIDADE>, <RESPONSAVEL_ESTADO_CIVIL>, <RESPONSAVEL_PROFISSAO>, titular do CPF nº. <RESPONSAVEL_CPF>, RG <RESPONSAVEL_RG>, residente à <RESPONSAVEL_ENDERECO> que por força do presente contrato passa a ser simplesmente denominado EMPREGADOR;\n' +
    +' \n' +
    +' 2.<TRABALHADOR_NOME>, <TRABALHADOR_NACIONALIDADE>, <TRABALHADOR_ESTADO_CIVIL>, <TRABALHADOR_CARGO>, titular do CPF nº. <TRABALHADOR_CPF>, RG <TRABALHADOR_RG>, CTPS <TRABALHADOR_CTPS>, residente à <TRABALHADOR_ENDERECO> doravante designado EMPREGADO;\n' +
    +' \n' +
    +' Firmam o presente CONTRATO INDIVIDUAL DE TRABALHO, nos termos da Lei e, seguintes cláusulas assim pactuadas:\n' +
    +' \n' +
    +' CLÁUSULA 1ª - Da Função\n' +
    +' \n' +
    +' O EMPREGADO obriga-se a prestar seus serviços no quadro de funcionários da EMPREGADORA para exercer as funções de <TRABALHADOR_CARGO>, mediante a remuneração de R$<TRABALHADOR_SALARIO>(<TRABALHADOR_SALARIO_EXTENSO>), a ser paga mensalmente ao empregado, até o 5º (quinto) dia útil do mês.\n' +
    +' \n' +
    +' Ressalva-se o EMPREGADOR, no direito de proceder à transferência do empregado para outro cargo ou função que entenda que este demonstre melhor capacidade de adaptação desde que compatível com sua condição pessoal.\n' +
    +' \n' +
    +' CLÁUSULA 2ª - Do Horário\n' +
    +' \n' +
    +' <JORNADA_DE_TRABALHO>\n' +
    +' \n' +
    +' Se houver horas extras, estas serão pagas na forma da lei ou serão compensadas com repouso correspondente.\n' +
    +' \n' +
    +' CLÁUSULA 3ª – Da Transferência\n' +
    +' \n' +
    +' O EMPREGADO está ciente e concorda que a prestação de seus serviços se dará tanto na localidade de celebração do Contrato de Trabalho, como em qualquer outra Cidade, Capital ou Vila do Território Nacional, nos termos do que dispõe o § 1° do artigo 469, da Consolidação das Leis do Trabalho.\n' +
    +' \n' +
    +' CLÁUSULA 4ª- Dos descontos\n' +
    +' \n' +
    +' O EMPREGADO autoriza o desconto em seu salário das importâncias que lhe forem adiantadas pelo empregador, bem como aos descontos legais, sobretudo, os previdenciários, de alimentação, habitação e vale transporte.\n' +
    +' \n' +
    ' Sempre que causar algum prejuízo, resultante de alguma conduta dolosa ou culposa ficará obrigada o EMPREGADO a ressarcir ao EMPREGADOR por todos os danos causados.\n' +
    ' \n' +
    ' CLÁUSULA 5ª - Das Disposições Especiais\n' +
    ' \n' +
    ' O EMPREGADO compromete-se também, a respeitar o regulamento da empresa, mantendo conduta irrepreensível no ambiente de trabalho, constituindo motivos para imediata dispensa do empregado, além dos previstos em lei, o desacato moral ou agressão física ao EMPREGADOR, ao administrador ou a pessoa de seus respectivos companheiros de trabalho, a embriaguez no serviço ou briga no local de trabalho.\n' +
    ' \n' +
    ' E por estarem assim contratados, nos termos de seus respectivos interesses, mandaram as partes lavrarem o presente instrumento que assinam na presença de 02 (duas) testemunhas, para as finalidades de direito.\n';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return CONTRATO_TRABALHO_TEMPO_INDETERMINADO;
  }
}
