import { AVISO_PREVIO_INDENIZADO } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosAvisoPrevioIndenizadoDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    ' Nos termos dos artigos 487 a 491 da C.L.T.,' +
    ' fica V.Sa. avisada que decidimos rescindir o seu contrato de trabalho,' +
    ' por não serem mais necessários os seus serviços.\n Desta forma,' +
    ' informamos que o Aviso Prévio será indenizado,' +
    ' devendo V.Sa. comparecer no dia <DATA_PAGAMENTO>,' +
    ' para receber as verbas rescisórias.\n\n' +
    " NOTA: ' Nos termos dos artigos 487 a 491 da C.L.T.,'sional para as devidas anotações.";

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return AVISO_PREVIO_INDENIZADO;
  }
}
