import { TERMO_ADITIVO } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosTermoAditivoDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    'Que entre si celebraram, de um lado <EMPRESA_NOME>, empresa inscrita no CNPJ <EMPRESA_CNPJ>, ' +
    +'com sede a <EMPRESA_ENDERECO> neste ato denominado EMPREGADOR, e de outro, <TRABALHADOR_NOME>, ' +
    +'inscrito no CPF sob o nro. <TRABALHADOR_CPF> , neste ato denominado EMPREGADO, mediante às seguintes ' +
    +'condições:\n ' +
    +'\n\n ' +
    +'<b>Cláusula Primeira</b>\n ' +
    +'\n ' +
    +'Fica alterada a Cláusula IV (Do Horário de Trabalho) passando, a partir desta data, a vigorar da ' +
    +'seguinte forma:\n ' +
    'O horário a ser obedecido é o seguinte: <TRABALHADOR_QUADRO_HORARIO>, perfazendo uma carga horária semanal de <TRABALHADOR_TOTAL_HORAS> ( <TRABALHADOR_TOTAL_HORAS_EXTENSO> );\n ' +
    '\n\n ' +
    '<b>Cláusula Segunda</b>\n ' +
    '\n ' +
    'Fica alterada a cláusula III, passando o empregado a receber a partir de <DATA> a importância ' +
    'de R$ <TRABALHADOR_SALARIO> ( <TRABALHADOR_SALARIO_EXTENSO> ) <TRABALHADOR_TIPO_PAGAMENTO>;\n ' +
    '\n\n ' +
    '<b>Cláusula Terceira</b>\n ' +
    '\n ' +
    'Ficam ratificadas as demais cláusulas do contrato em questão, desde que não contrariem o que ficou ' +
    'convencionado no presente Termo Aditivo.\n ' +
    '\n ' +
    'E, por estarem assim, justos e acordados, firmam o presente Termo Aditivo ao Contrato de Trabalho, ' +
    'em 2 (duas) vias de igual teor, para que produzam seus jurídicos e legais efeitos, na presença de 2 ' +
    '(duas) testemunhas igualmente subscritas.\n' +
    '\n\n' +
    '<DATA_EXTENSO>, <EMPRESA_CIDADE>';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return TERMO_ADITIVO;
  }
}
