import { AVISO_FERIAS } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosAvisoFeriasDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    'Comunicado a <NOME_TRABALHADOR> :\n\n' +
    'De acordo com o artigo 135 da Lei n. 7414, de 09/12/85, fica avisado de que sairá de férias no dia <DT_INICIO_GOZO> voltando a trabalhar no dia  <DT_FIM_GOZO>, ' +
    'férias correspondente(s) a(os) <MESES_PER_AQUISITIVO> mes(es) de trabalho completados em <DATA_FIM_PER_AQUISITIVO>.';

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return AVISO_FERIAS;
  }
}
