import { CONTRATO_TRABALHO_INTERMITENTE } from '../../enumerated/mfp/customizacaoTermosTipoEnum';
import { CustomizacaoTermosDefaultContent } from './customizacaoTermosDefaultContent';

export class CustomizacaoTermosContratoTrabalhoIntermitenteDefault implements CustomizacaoTermosDefaultContent {
  private TERMO_PADRAO: string =
    'Por este instrumento particular, que entre si fazem a  <EMPRESA_NOME>, pessoa jurídica de direito privado, inscrita no CNPJ sob o nº  <EMPRESA_CNPJ>, com sede à  <EMPRESA_ENDERECO>, doravante denominada EMPREGADORA, e de outro lado  <TRABALHADOR_NOME>, <TRABALHADOR_NACIONALIDADE>,  estado civil <TRABALHADOR_ESTADO_CIVIL>,  <TRABALHADOR_CARGO_NOME>, inscrito(a) no CPF sob o nº  <TRABALHADOR_CPF>, no RG nº  <TRABALHADOR_RG>  e portador da CTPS nº  <TRABALHADOR_CTPS>, Série  <TRABALHADOR_CTPS_SERIE>, residente e domiciliado(a)  <TRABALHADOR_ENDERECO>, daqui em diante denominado(a) EMPREGADO(a), fica justo e acordado o contrato de trabalho intermitente nos termos seguintes:\n\nI - O(a) EMPREGADO(a) é contratado(a) na modalidade de trabalho intermitente, conforme artigo 443 e seu parágrafo 3º, e artigo 452-A e seus parágrafos, da CLT.' +
    +'\n\nII - O(a) EMPREGADO(a) exercerá a função de <TRABALHADOR_CARGO_NOME>, com todas as atribuições que lhe são peculiares, bem como as que vierem a ser designadas através de instruções da EMPREGADORA.' +
    +'\n\nIII- O(a) EMPREGADO(a) receberá o salário de R$ <TRABALHADOR_SALARIO>( <TRABALHADOR_SALARIO_EXTENSO> ) .' +
    +'\n\nIV - A EMPREGADORA convocará o(a) EMPREGADO(a) por meio de comunicação eficaz, informando a jornada solicitada, com antecedência de pelo menos três dias. Recebida a comunicação o(a) EMPREGADO(a) terá um dia útil para comunicar a aceitação ou não da proposta, sendo que seu silêncio representará a recusa.' +
    +'\n\nV -  Aceita a proposta, a parte que, sem justo motivo, descumprir o ajustado, pagará à outra parte, no prazo de trinta dias, multa de 50% (cinqüenta por cento) da remuneração que seria devida,  permitida a compensação em igual prazo.' +
    +'\n\nVI - O período de inatividade não será considerado tempo à disposição da EMPREGADORA, podendo o(a) EMPREGADO(a) prestar serviços a outros contratantes.' +
    +'\n\nVII- E, por estarem de pleno acordo, assinam ambas as partes este contrato, em duas vias de igual teor na presença das testemunhas abaixo relacionadas.' +
    +'\n\n\n\n    ________________________________________' +
    +'\n    Local/Data' +
    +'\n\n\n    ________________________________________                    ________________________________________' +
    +'\n    Testemunha 1                                                                             Testemunha 2' +
    +'\n\n\n\n    ________________________________________                    ________________________________________' +
    "\n    Empregador                                                                                Empregado'";

  public getTermoDefault() {
    return this.TERMO_PADRAO;
  }

  public getCustomizacaoTermosTipoEnum() {
    return CONTRATO_TRABALHO_INTERMITENTE;
  }
}
